import React from "react";
import { Box, Typography, Grid, Paper, useMediaQuery } from "@mui/material";
import { ThemeProvider, createTheme } from "@mui/material/styles";

const theme = createTheme({
  palette: {
    background: {
      default: "transparent",
    },
    text: {
      primary: "#fff",
    },
  },

  typography: {
    fontFamily: "Arial, sans-serif",
  },
  components: {
    MuiPaper: {
      styleOverrides: {
        root: {
          borderRadius: "40px",
        },
      },
    },
  },

});

const DashboardItem = ({ title, value }: { title: string; value: string }) => {
  const isMobile = useMediaQuery(theme.breakpoints.down("md"));

  return (
    <Paper
      elevation={3}
      sx={{
        padding: isMobile ? 1 : "32px 16px",
        backgroundColor: "#4A1E2A",
        color: theme.palette.text.primary,
        textAlign: "center",
        height: "100%",
        display: "flex",
        flexDirection: "column",
        justifyContent: "space-evenly",
        alignItems: "center",
        boxShadow: "10px 10px 10px 0px rgba(20, 20, 20, 0.3)",
      }}
    >
      <Typography sx={{ wordBreak: "break-word", fontSize: { xs: 14, sm: 18, md: 28 } }}>
        {title}
      </Typography>
      <Typography sx={{ fontSize: { xs: 14, sm: 18, md: 52 } }}>{value}</Typography>
    </Paper>
  );
};

const RightSideItem = ({ label, value }: { label: string; value: string }) => {
  const isMobile = useMediaQuery(theme.breakpoints.down("md"));
  return (
    <Box sx={{ textAlign: "left", mt: 1 }}>
      <Typography sx={{ textAlign: "center" }} variant={isMobile ? "h6" : "h4"}>{label}</Typography>
      <Typography sx={{ textAlign: "center" }} variant={isMobile ? "h5" : "h3"}>{value}</Typography>
    </Box>
  );
};

const App: React.FC = () => {
  const isMobile = useMediaQuery(theme.breakpoints.down("md"));

  return (
    <ThemeProvider theme={theme}>
      <Box
        sx={{
          backgroundColor: theme.palette.background.default,
          py: isMobile ? 2 : 4,
        }}
      >
        <Box
          sx={{
            display: 'flex',
            flexDirection: { xs: 'column', sm: 'row' },
            justifyContent: 'space-between',
            alignItems: 'center'
          }}>
          <Typography
            variant="h4"
            sx={{
              textAlign: "left",
              color: theme.palette.text.primary,
              marginBottom: 2,
            }}
          >
            Dashboard
          </Typography>
          <Typography
            variant="h4"
            sx={{
              textAlign: "left",
              color: theme.palette.text.primary,
              marginBottom: 2,
              fontSize: 18
            }}
          >
            View on wax.bloks.io
          </Typography>
        </Box>

        <Grid container spacing={2} height="100%">
          {/* Left Side */}
          <Grid item xs={12} sm={6} md={7}>
            <Grid container spacing={2}>
              <Grid item xs={12} sm={12} md={6}>
                <DashboardItem title="Requested CPU for self total:" value="6.12354" />
              </Grid>
              <Grid item xs={12} sm={12} md={6}>
                <Grid sx={{ marginBottom: 2 }}>
                  <DashboardItem title="Update Collected Fees" value="" />
                </Grid>
                <Grid sx={{ marginTop: 2 }}>
                  <DashboardItem title="Request Free CPU" value="" />
                </Grid>
              </Grid>

              <Grid item xs={12} sm={12} md={6}>
                <DashboardItem title="Requested CPU for other total:" value="6.12354" />
              </Grid>
              <Grid item xs={12} sm={12} md={6}>
                <DashboardItem title="Deposited WAX to earn:" value="6.12354" />
              </Grid>
            </Grid>
          </Grid>

          {/* Right Side */}
          <Grid item xs={12} sm={6} md={5}>
            <Paper
              elevation={3}
              sx={{
                padding: isMobile ? 1 : 2,
                backgroundColor: "#831F3F",
                color: theme.palette.text.primary,
                textAlign: "left",
                height: "100%",
                display: "flex",
                flexDirection: "column",
                justifyContent: "center",
                alignItems: "center",
              }}
            >
              <Typography variant={isMobile ? "h6" : "h4"} fontWeight="bold">
                Total Free CPU
              </Typography>
              <Typography variant={isMobile ? "h4" : "h3"}>6.12354</Typography>
              <RightSideItem label="1 Day Earning" value="6.12354" />
              <RightSideItem label="15 Days Earning" value="6.12354" />
              <RightSideItem label="30 Days Earning" value="6.12354" />
            </Paper>
          </Grid>
        </Grid>
      </Box>
    </ThemeProvider>
  );
};

export default App;
