import React from 'react';
import { Box, Typography, Card, CardContent, useTheme, Hidden } from '@mui/material';

// Types
interface Feature {
	title: string;
	description: string;
	backgroundColor?: string;
	width?: string;
}

// Theme Constants
const THEME = {
	colors: {
		primary: 'rgb(45,25,30)',
		secondary: 'rgb(74,30,42)',
		text: {
			primary: '#ffffff',
			secondary: '#bbb',
			tertiary: '#ccc'
		}
	},
	spacing: {
		gap: '16px'
	}
};

// Styled Components
const FeatureCard: React.FC<Feature> = ({ title, description, backgroundColor, width = '100%' }) => (
	<Card
		sx={{
			backgroundColor: backgroundColor || THEME.colors.primary,
			color: THEME.colors.text.primary,
			height: '100%',
			display: 'flex',
			alignItems: 'center',
			justifyContent: 'center',
			textAlign: 'center',
			borderRadius: 8,
			border: '2px solid #bbb',
			width,
			transition: 'transform 0.2s ease-in-out, box-shadow 0.2s ease-in-out',
			'&:hover': {
				transform: 'translateY(-4px)',
				boxShadow: '0 4px 20px rgba(0, 0, 0, 0.2)'
			}
		}}
	>
		<CardContent>
			<Typography
				variant="h4"
				gutterBottom
				sx={{
					fontSize: { xs: 24, sm: 28, md: 30 },
					fontWeight: 500,
					mb: 2
				}}
			>
				{title}
			</Typography>
			<Typography
				variant="body2"
				sx={{
					fontSize: { xs: 13, sm: 14, md: 15 },
					color: THEME.colors.text.secondary,
					lineHeight: 1.6
				}}
			>
				{description}
			</Typography>
		</CardContent>
	</Card>
);

const Features = () => {
	const features: Feature[] = [
		{
			title: "Request CPU for Self",
			description: "The system dynamically creates a price for the staked wax given the current supply, total loaned out, number of days, and amount user requests. Then that wax is sent to a minion account and staked to the user for the given time.",
			width: '100%'
		},
		{
			title: "Request Free CPU",
			description: "As long as this account has wax it will send in a specified amount to the main contract requesting CPU as if it was a regular user.",
			width: '50%'
		},
		{
			title: "Deposit to Earn",
			description: "Users can also deposit wax to earn 70% of the total fees collected.",
			width: '50%'
		},
		{
			title: "Request CPU for Others",
			description: "Similar to requesting for yourself, this does the first few steps but then directly stakes to the user specified. This can be sent from anyone for anyone else.",
			backgroundColor: THEME.colors.secondary,
		},
		{
			title: "Update Collected Fees",
			description: "This method is not required but will immediately update the table entry with how much wax the user has. This function will update the fees in the contract table and show you how much you could withdraw in total.",
			backgroundColor: THEME.colors.secondary,
		},
		{
			title: "Withdraw Deposited Wax",
			description: "At any time a user can request to withdraw deposited wax. A user can make as many withdrawal requests as they want up to the total wax they have deposited. If there is not enough liquid wax, as more gets unstaked it will be sent in order to users in line to withdraw.",
			backgroundColor: THEME.colors.secondary,
		}
	];

	return (
		<Hidden smDown>
			<Box
				sx={{
					py: 3,
				}}
			>
				<Typography
					variant="h2"
					align="center"
					gutterBottom
					sx={{
						fontSize: { xs: 22, sm: 28, md: 36 },
						fontWeight: 'bold',
						mb: 3
					}}
				>
					Users can interact with the smart contract in a <br /> number of ways
				</Typography>

				<Typography
					align="center"
					paragraph
					sx={{
						fontSize: { xs: 12, sm: 16, md: 22 },
						color: THEME.colors.text.tertiary,
						mb: 6
					}}
				>
					The system will be open for 1 week before allowing users to deposit their own wax to earn. If <br />
					users find bugs there is a bug bounty program on discord.
				</Typography>

				<Box
					sx={{
						display: 'flex',
						flexDirection: 'column',
						gap: THEME.spacing.gap
					}}
				>
					{/* First Row */}
					<Box
						sx={{
							display: 'flex',
							flexDirection: { sm: 'column', md: 'row' },
							gap: THEME.spacing.gap,
							flexWrap: 'wrap'
						}}
					>
						<Box sx={{ flex: { xs: '0 0 100%', md: '0 0 50%' }, maxWidth: { md: 'calc(50% - 8px)' } }}>
							<FeatureCard {...features[0]} />
						</Box>
						<Box
							sx={{
								flex: { xs: '0 0 100%', md: '0 0 50%' },
								maxWidth: { md: 'calc(50% - 8px)' },
								display: 'flex',
								gap: THEME.spacing.gap
							}}
						>
							<Box sx={{ flex: 1 }}>
								<FeatureCard {...features[1]} width="100%" />
							</Box>
							<Box sx={{ flex: 1 }}>
								<FeatureCard {...features[2]} width="100%" />
							</Box>
						</Box>
					</Box>
					{/* second row */}
					<Box
						sx={{
							display: 'flex',
							flexDirection: { sm: 'column', md: 'row' },
							gap: THEME.spacing.gap,
							flexWrap: 'wrap'
						}}
					>
						<Box
							sx={{
								flex: { xs: '0 0 100%', md: '0 0 50%' },
								maxWidth: { md: 'calc(50% - 8px)' },
								display: 'flex',
								gap: THEME.spacing.gap
							}}
						>
							<Box sx={{ flex: 1 }}>
								<FeatureCard {...features[4]} width="100%" />
							</Box>
							<Box sx={{ flex: 1 }}>
								<FeatureCard {...features[3]} width="100%" />
							</Box>
						</Box>
						<Box sx={{ flex: { xs: '0 0 100%', md: '0 0 50%' }, maxWidth: { md: 'calc(50% - 8px)' } }}>
							<FeatureCard {...features[5]} />
						</Box>
					</Box>
				</Box>
			</Box>
		</Hidden>
	);
};

export default React.memo(Features);
