import Dashboard from '../view/Dashboard';
import Error from '../view/Error';
import Login from '../view/Login';
import Account from '../view/Account';
import AboutUs from '../view/AboutUs';
import LimitlessWax from '../view/LimitlessWax';
import Cpu4sale from '../view/Cpu4sale';
export const routes = [
  {
    path: '/',
    component: Dashboard,
  },
  {
    path: '/account',
    component: Account,
  },
  {
    path: '/login',
    component: Login,
  },
  {
    path: '/error',
    component: Error,
  },
  {
    path: '/about',
    component: AboutUs,
  },
  {
    path: '/limitlesswax',
    component: LimitlessWax,
  },
  {
    path: '/cpu4sale',
    component: Cpu4sale,
  },
];

