import React from "react";
import { Box, Typography, Paper, useTheme, useMediaQuery } from "@mui/material";

// Types
interface BalanceCardProps {
  label: string;
  value: string;
}

// Theme Constants
const THEME = {
  colors: {
    primary: '#8C243F',
    secondary: '#391E24',
    text: {
      primary: '#ffffff'
    }
  },
  sizes: {
    avatar: {
      mobile: 80,
      desktop: 170
    },
    spacing: {
      xs: 8,
      sm: 16,
      md: 24
    }
  }
};

// Styled Components
const BalanceCard: React.FC<BalanceCardProps> = ({ label, value }) => {
  const isMobile = useMediaQuery('(max-width:600px)');

  return (
    <Paper
      elevation={3}
      sx={{
        padding: isMobile ? 2 : 3,
        borderRadius: 4,
        backgroundColor: THEME.colors.secondary,
        color: THEME.colors.text.primary,
        flex: 1,
        minWidth: { xs: '100%', sm: 'calc(50% - 8px)' },
        transition: 'transform 0.2s ease-in-out',
        '&:hover': {
          transform: 'translateY(-4px)'
        }
      }}
    >
      <Typography
        variant="body1"
        sx={{
          fontSize: { xs: 18, sm: 20, md: 22 },
          textAlign: 'left',
          mb: 1
        }}
      >
        {label}
      </Typography>
      <Typography
        variant="h6"
        sx={{
          fontSize: { xs: 18, sm: 20, md: 22 },
          fontWeight: 'bold'
        }}
      >
        {value}
      </Typography>
    </Paper>
  );
};

const HeroAccount: React.FC = () => {
  const isMobile = useMediaQuery('(max-width:600px)');

  return (
    <Box
      sx={{
        backgroundColor: 'transparent',
        py: { xs: 2, sm: 3, md: 4 },
        px: { xs: 2, sm: 3 },
        maxWidth: '1200px',
        margin: '0 auto'
      }}
    >
      <Box
        sx={{
          display: 'flex',
          flexDirection: { xs: 'column', md: 'row' },
          alignItems: { xs: 'flex-start', md: 'center' },
          justifyContent: 'space-between',
          gap: { xs: 3, sm: 4 }
        }}
      >
        {/* Avatar and Account Name */}
        <Box
          sx={{
            display: 'flex',
            alignItems: 'center',
            gap: { xs: 2, sm: 3 },
            width: { xs: '100%', md: 'auto' }
          }}
        >
          <Box
            sx={{
              width: isMobile ? THEME.sizes.avatar.mobile : THEME.sizes.avatar.desktop,
              height: isMobile ? THEME.sizes.avatar.mobile : THEME.sizes.avatar.desktop,
              backgroundColor: THEME.colors.primary,
              borderRadius: '50%',
              boxShadow: '5px 5px 15px 2px rgba(30, 30, 30, 0.3)',
              transition: 'transform 0.3s ease-in-out',
              '&:hover': {
                transform: 'scale(1.05)'
              }
            }}
          />
          <Typography
            variant={isMobile ? "h6" : "h4"}
            sx={{
              color: THEME.colors.text.primary,
              fontWeight: 'medium'
            }}
          >
            ki.fg.wam
          </Typography>
        </Box>

        {/* Balance Cards */}
        <Box
          sx={{
            display: 'flex',
            gap: 2,
            flexWrap: 'wrap',
            flex: { xs: '1 1 100%', md: '0 1 auto' },
            maxWidth: { xs: '100%', md: '500px' }
          }}
        >
          <BalanceCard
            label="Total WAX Balance:"
            value="6.12354672"
          />
          <BalanceCard
            label="Total USD Balance:"
            value="$1.9321245"
          />
        </Box>
      </Box>
    </Box>
  );
};

export default React.memo(HeroAccount);
