import React, { useState, useMemo } from 'react';
import {
  styled,
  IconButton,
  Typography,
  Button,
  Box,
  useMediaQuery,
  useTheme,
  Drawer,
  List,
  ListItem,
  ListItemButton,
  ListItemIcon,
  ListItemText,
  Link,
} from '@mui/material';
import MenuIcon from '@mui/icons-material/Menu';
import InfoIcon from '@mui/icons-material/Info';
import AccountCircleIcon from '@mui/icons-material/AccountCircle';
import ShoppingCartIcon from '@mui/icons-material/ShoppingCart';
import WavesIcon from '@mui/icons-material/Waves';

// Types and Interfaces
interface HeaderProps {
  user?: string;
}

interface NavItem {
  text: string;
  icon: JSX.Element;
  path: string;
}

interface DrawerContentProps {
  onClose: () => void;
  items: NavItem[];
  username: string;
}

// Theme Constants
const THEME = {
  colors: {
    primary: '#1976d2',
    text: {
      primary: '#000000',
      secondary: '#ffffff'
    }
  },
  sizes: {
    drawer: {
      width: 240
    }
  }
};

// Styled Components
const StyledButton = styled(Button)(({ theme }) => ({
  borderRadius: 3,
  fontSize: 16,
  textTransform: 'none',
  transition: 'all 0.2s ease-in-out',
  '&:hover': {
    transform: 'translateY(-2px)',
    backgroundColor: 'rgba(255, 255, 255, 0.1)'
  }
}))
// Component for the drawer content
const DrawerContent: React.FC<DrawerContentProps> = ({ onClose, items, username }) => (
  <Box
    onClick={onClose}
    sx={{
      textAlign: 'center',
      backgroundColor: '#f5f5f5',
      height: '100%'
    }}
  >
    <Typography
      variant="h6"
      sx={{
        my: 2,
        color: THEME.colors.text.primary,
        fontWeight: 'medium'
      }}
    >
      {username}
    </Typography>
    <List>
      {items.map((item) => (
        <ListItem key={item.text} disablePadding>
          <ListItemButton
            component={Link}
            href={item.path}
            sx={{
              textAlign: 'left',
              transition: 'background-color 0.2s ease-in-out',
              '&:hover': {
                backgroundColor: 'rgba(25, 118, 210, 0.08)'
              }
            }}
          >
            <ListItemIcon sx={{ color: THEME.colors.primary }}>
              {item.icon}
            </ListItemIcon>
            <ListItemText
              primary={item.text}
              sx={{
                color: THEME.colors.text.primary,
                '& .MuiTypography-root': {
                  fontWeight: 500
                }
              }}
            />
          </ListItemButton>
        </ListItem>
      ))}
    </List>
  </Box>
);

// Main Component
const Header: React.FC<HeaderProps> = ({ user = 'Login' }) => {
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('sm'));
  const [drawerOpen, setDrawerOpen] = useState(false);

  const handleDrawerToggle = () => {
    setDrawerOpen(!drawerOpen);
  };

  // Memoize nav items to prevent unnecessary re-renders
  const navItems = useMemo(() => [
    { text: 'About Us', icon: <InfoIcon />, path: '/about' },
    { text: 'Account', icon: <AccountCircleIcon />, path: '/account' },
    { text: 'Cpu4sale', icon: <ShoppingCartIcon />, path: '/cpu4sale' },
    { text: 'Limitlesswax', icon: <WavesIcon />, path: '/limitlesswax' },
  ], []);

  return (
    <Box
      component="header"
      sx={{
        backgroundColor: 'transparent',
        boxShadow: 'none',
        py: { xs: 2, sm: 4 },
        position: 'relative',
        zIndex: theme.zIndex.appBar
      }}
    >
      <Box sx={{ display: 'flex', justifyContent: 'space-between', width: '100%', margin: '0 auto', }}>
        <Box sx={{ display: 'flex', alignItems: 'center' }}>
          <IconButton
            edge="start"
            color="inherit"
            aria-label="open menu"
            onClick={handleDrawerToggle}
            sx={{
              mr: 2,
              display: { sm: 'none' },
              '&:hover': {
                backgroundColor: 'rgba(255, 255, 255, 0.1)'
              }
            }}
          >
            <MenuIcon />
          </IconButton>
          <Link
            href="/"
            sx={{
              display: { xs: 'none', sm: 'block' },
              '&:hover': {
                opacity: 0.8
              }
            }}
          >
            <Box
              component="img"
              src="images/logo.png"
              alt="Logo"
              sx={{
                height: 60,
                width: 'auto',
                transition: 'opacity 0.2s ease-in-out'
              }}
            />
          </Link>
        </Box>

        {!isMobile && (
          <Box
            sx={{
              display: 'flex',
              alignItems: 'center',
              gap: 1
            }}
          >
            {navItems.map((item) => (
              <StyledButton
                key={item.text}
                onClick={() => window.location.href = item.path}
                color="inherit"
              >
                {item.text}
              </StyledButton>
            ))}
            <StyledButton
              onClick={() => window.location.href = '/login'}
              color="inherit"
              variant="outlined"
              sx={{
                borderColor: 'currentColor',
                '&:hover': {
                  borderColor: 'currentColor',
                  backgroundColor: 'rgba(255, 255, 255, 0.1)'
                }
              }}
            >
              {user}
            </StyledButton>
          </Box>
        )}
      </Box>

      <Drawer
        variant="temporary"
        anchor="left"
        open={drawerOpen}
        onClose={handleDrawerToggle}
        ModalProps={{
          keepMounted: true // Better open performance on mobile
        }}
        sx={{
          display: { xs: 'block', sm: 'none' },
          '& .MuiDrawer-paper': {
            boxSizing: 'border-box',
            width: THEME.sizes.drawer.width,
            borderRadius: '0 16px 16px 0'
          }
        }}
      >
        <DrawerContent
          onClose={handleDrawerToggle}
          items={navItems}
          username={user}
        />
      </Drawer>
    </Box>
  );
};

export default React.memo(Header);
