import React from "react";
import { Box, Button, Typography, useMediaQuery } from "@mui/material";
import { ThemeProvider, createTheme } from "@mui/material/styles";
import ArrowForwardIosIcon from "@mui/icons-material/ArrowForwardIos";

const theme = createTheme({
  palette: {
    background: {
      default: "transparent",
    },
    text: {
      primary: "#ffffff",
    },
  },
  typography: {
    fontFamily: "Arial, sans-serif",
  },
});

const NavButton = ({ text, link }: { text: string; link: string }) => {
  const isMobile = useMediaQuery(theme.breakpoints.down("sm"));

  return (
    <Button
      href={link}
      variant="contained"
      sx={{
        width: "100%",
        backgroundColor: "#5C2736",
        color: theme.palette.text.primary,
        borderRadius: "32px",
        padding: isMobile ? "8px 16px" : "40px 70px",
        display: "flex",
        alignItems: "center",
        justifyContent: "space-between",
        textTransform: "none",
        boxShadow: "5px 5px 5px rgba(20, 20, 20, 0.4)",
        "&:hover": {
          backgroundColor: "#720026",
        },
      }}
    >
      <Typography variant={isMobile ? "body1" : "h5"} sx={{ mr: 1 }}>
        {text}
      </Typography>
      <ArrowForwardIosIcon fontSize={isMobile ? "small" : "medium"} />
    </Button>
  );
};

const ButtonAccount: React.FC = () => {
  const isMobile = useMediaQuery(theme.breakpoints.down("sm"));

  return (
    <ThemeProvider theme={theme}>
      <Box
        sx={{
          backgroundColor: theme.palette.background.default,
          display: "flex",
          flexDirection: isMobile ? "column" : "row",
          alignItems: "center",
          justifyContent: "space-between",
          gap: isMobile ? 1 : 2,
          py: isMobile ? 2 : 4,
        }}
      >
        <NavButton text="Go to Cpu4Sale" link="https://cpu4sale.com" />
        <NavButton text="Go to LimitlessWAX" link="https://limitlesswax.com" />
      </Box>
    </ThemeProvider>
  );
};

export default ButtonAccount;
