import React, { useState, useMemo } from 'react';
import {
  Box,
  Grid,
  TextField,
  Button,
  Typography,
  Paper,
  Tabs,
  Tab,
  useMediaQuery,
  useTheme,
  Container,
  InputLabel,
  styled
} from '@mui/material';

// Types and Interfaces
interface TabPanelProps {
  children?: React.ReactNode;
  index: number;
  value: number;
}

interface InputFieldProps {
  label: string;
  value: string;
  onChange: (value: string) => void;
  width?: string;
  unit?: string;
}

interface StatsCardProps {
  value: string;
  label: string;
}

// Styled Components
const StyledButton = styled(Button)(({ theme }) => ({
  fontSize: 16,
  backgroundColor: '#882140',
  border: '1px solid #bbb',
  borderRadius: 0,
  color: 'white',
  padding: '5px 0',
  '&:hover': {
    backgroundColor: '#C1668B',
  },
}));

const StyledTextField = styled(TextField)({
  '& .MuiInputBase-root': {
    backgroundColor: '#831F3F',
  },
  '& .MuiInputBase-input': {
    color: 'white',
  },
  '& .MuiOutlinedInput-notchedOutline': {
    border: 'none',
  },
  '&:hover .MuiOutlinedInput-notchedOutline': {
    border: 'none',
  },
  '&.Mui-focused .MuiOutlinedInput-notchedOutline': {
    border: 'none',
  }
});

// Reusable Components
const StatsCard: React.FC<StatsCardProps> = ({ value, label }) => {
  const isSmallScreen = useMediaQuery(useTheme().breakpoints.down('sm'));

  return (
    <Paper
      elevation={3}
      sx={{
        p: 2,
        borderRadius: 5,
        bgcolor: '#5C2736',
        textAlign: 'center',
        color: 'white',
        boxShadow: '0 3px 15px rgba(30,30,30,0.3)',
        flex: 1
      }}
    >
      <Typography variant={isSmallScreen ? "h6" : "h5"}>{value}</Typography>
      <Typography variant="body2">{label}</Typography>
    </Paper>
  );
};

const InputField: React.FC<InputFieldProps> = ({ label, value, onChange, width = 'auto', unit }) => {
  return (
    <Box>
      <InputLabel sx={{ color: 'white', mb: 1, fontSize: '1.2rem' }}>{label}</InputLabel>
      <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
        <StyledTextField
          variant="outlined"
          size="small"
          value={value}
          onChange={(e) => onChange(e.target.value)}
          sx={{ width }}
        />
        {unit && (
          <Typography variant="body2" sx={{ mt: 0.5, color: 'gray.400' }}>{unit}</Typography>
        )}
      </Box>
    </Box>
  );
};

const TabPanel: React.FC<TabPanelProps> = ({ children, value, index }) => {
  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`tab-panel-${index}`}
      aria-labelledby={`tab-${index}`}
    >
      {value === index && (
        <Box sx={{ p: 3 }}>
          {children}
        </Box>
      )}
    </div>
  );
};

// Main Component
function WaxStaking() {
  const [amountToSend, setAmountToSend] = useState('');
  const [numberOfDays, setNumberOfDays] = useState('');
  const [userToStake, setUserToStake] = useState('');
  const [tabValue, setTabValue] = useState(0);

  const theme = useTheme();
  const isSmallScreen = useMediaQuery(theme.breakpoints.down('sm'));

  const stats = useMemo(() => ({
    totalWaxInSystem: '517892.24469852',
    amountToBeStaked: '11.349123158',
    roi: '11.349',
    waxBalance: '11.3491',
  }), []);

  const handleTabChange = (event: React.SyntheticEvent, newValue: number) => {
    setTabValue(newValue);
  };

  const handleRequestStake = () => {
    console.log('Requesting Stake:', { amountToSend, numberOfDays });
  };

  const tabProps = useMemo(() => [
    {
      label: "Request CPU for Self", component: (
        <Box sx={{ display: 'flex', flexDirection: { xs: 'column', sm: 'row' }, justifyContent: 'space-between', gap: 2 }}>
          <Box sx={{ display: 'flex', flexDirection: 'column', gap: 2 }}>
            <InputField
              label="Amount to send"
              value={amountToSend}
              onChange={setAmountToSend}
              unit="WAX"
            />
            <InputField
              label="Number of days"
              value={numberOfDays}
              onChange={setNumberOfDays}
              width="70px"
              unit="Days"
            />
            <Typography sx={{ mt: 1, fontSize: 18 }}>
              Amount to be staked <br /> {stats.amountToBeStaked} WAX
            </Typography>
            <StyledButton
              onClick={handleRequestStake}
              sx={{ width: '165px' }}
            >
              Request self stake
            </StyledButton>
          </Box>
          <Paper sx={{
            p: 2,
            bgcolor: '#831F3F',
            textAlign: 'center',
            color: 'white',
            borderRadius: 5,
            padding: '16px 35px',
          }}>
            <Typography sx={{ fontSize: { xs: '1.2rem', sm: '1.5rem' }, fontWeight: 'bold' }}>ROI</Typography>
            {['Daily', 'Weekly', 'Monthly'].map((period) => (
              <React.Fragment key={period}>
                <Typography>{period}</Typography>
                <Typography sx={{ fontSize: '1rem', fontWeight: 'bold' }}>{stats.roi} WAX</Typography>
              </React.Fragment>
            ))}
          </Paper>
        </Box>
      )
    },
    {
      label: "Stake to User", component: (
        <Box sx={{ display: 'flex', flexDirection: 'column', gap: 2 }}>
          <InputField
            label="Amount to send"
            value={amountToSend}
            onChange={setAmountToSend}
            unit="WAX"
          />
          <Box sx={{ display: 'flex', flexDirection: { xs: 'column', sm: 'row' }, gap: 2 }}>
            <InputField
              label="Number of days"
              value={numberOfDays}
              onChange={setNumberOfDays}
              width="70px"
              unit="Days"
            />
            <InputField
              label="User to stake to"
              value={userToStake}
              onChange={setUserToStake}
              width="200px"
            />
          </Box>
          <Typography sx={{ mt: 1, fontSize: 18 }}>
            Amount to be staked <br /> {stats.amountToBeStaked} WAX
          </Typography>
          <StyledButton
            onClick={handleRequestStake}
            sx={{ width: '140px' }}
          >
            Stake to user
          </StyledButton>
        </Box>
      )
    },
    {
      label: "Deposit and Earn", component: (
        <Box sx={{ display: 'flex', flexDirection: 'column', gap: 2 }}>
          <Box sx={{ display: 'flex', alignItems: 'center', gap: 3 }}>
            <Typography sx={{ mt: 1, fontSize: 18 }}>
              WAX Balance <br /> {stats.waxBalance} WAX
            </Typography>
            <Typography sx={{ mt: 1, fontSize: 18 }}>
              WAX in System <br /> {stats.waxBalance} WAX
            </Typography>
          </Box>
          <InputField
            label="Amount to send"
            value={amountToSend}
            onChange={setAmountToSend}
            unit="WAX"
          />
          <Box mb={10}>
            <StyledButton sx={{ width: '180px' }}>Deposit and Earn</StyledButton>
          </Box>
        </Box>
      )
    },
    {
      label: "Withdraw", component: (
        <Box sx={{ display: 'flex', flexDirection: 'column', gap: 2 }}>
          <Typography sx={{ mt: 1, fontSize: 18 }}>
            WAX in System <br /> {stats.waxBalance} WAX
          </Typography>
          <InputField
            label="Amount to send"
            value={amountToSend}
            onChange={setAmountToSend}
            unit="WAX"
          />
          <Box mb={10}>
            <StyledButton sx={{ width: '130px' }}>Withdraw</StyledButton>
          </Box>
        </Box>
      )
    }
  ], [amountToSend, numberOfDays, userToStake, stats, handleRequestStake]);

  return (
    <Box sx={{ color: 'white', py: 3, borderRadius: 2 }}>
      <Box sx={{ display: 'flex', flexDirection: 'column', gap: 3 }}>
        <Container maxWidth="sm">
          <Box sx={{ display: 'flex', flexDirection: { xs: 'column', sm: 'row' }, gap: 3 }}>
            <StatsCard value={stats.totalWaxInSystem} label="Total Wax in System" />
            <StatsCard value={stats.totalWaxInSystem} label="Total Wax in System" />
          </Box>
        </Container>

        <Box sx={{ display: 'flex', flexDirection: { xs: 'column', md: 'row' }, gap: 3 }}>
          <Box sx={{
            display: 'flex',
            flexDirection: { xs: 'column', sm: 'row' },
            bgcolor: '#5C2736',
            color: 'white',
            flex: { xs: '1', md: '2' }
          }}>
            <Tabs
              orientation={isSmallScreen ? "horizontal" : "vertical"}
              variant="scrollable"
              value={tabValue}
              onChange={handleTabChange}
              sx={{
                borderColor: 'divider',
                backgroundColor: '#831F3F',
                '& .MuiTabs-indicator': { display: 'none' },
                '& .Mui-selected': {
                  backgroundColor: '#391E24',
                  color: 'white !important'
                }
              }}
            >
              {tabProps.map((tab, index) => (
                <Tab
                  key={index}
                  label={tab.label}
                  sx={{
                    fontSize: { xs: 10, sm: 14, md: 16 },
                    color: 'white',
                    textAlign: 'left',
                    display: 'block',
                    opacity: 1,
                    '&:hover': { opacity: 0.8 }
                  }}
                />
              ))}
            </Tabs>

            <Box sx={{ flex: 1 }}>
              {tabProps.map((tab, index) => (
                <TabPanel key={index} value={tabValue} index={index}>
                  {tab.component}
                </TabPanel>
              ))}
            </Box>
          </Box>
        </Box>
      </Box>
    </Box>
  );
}

export default WaxStaking;